@font-face {
  font-family: "M PLUS 1";
  font-weight: 100 900;
  font-display: swap;
  font-style: normal;
  font-named-instance: "Regular";
  src: url("Mplus1-Regular.woff2") format("woff2");
}

@font-face {
  font-family: "M PLUS 1";
  font-weight: 600;
  font-display: swap;
  font-style: normal;
  font-named-instance: "Regular";
  src: url("Mplus1-SemiBold.woff2") format("woff2");
}

@font-face {
  font-family: "M PLUS 1 Code";
  font-weight: 100 900;
  font-display: swap;
  font-style: normal;
  font-named-instance: "Regular";
  src: url("Mplus1Code-Regular.woff2") format("woff2");
}

@font-face {
  font-family: "M PLUS 1 Code";
  font-weight: 600;
  font-display: swap;
  font-style: normal;
  font-named-instance: "Regular";
  src: url("Mplus1Code-SemiBold.woff2") format("woff2");
}

$grid-color: 200;

html {
  background-size: 1.75rem 1.75rem;
  background-image: linear-gradient(
      to right,
      rgba($grid-color, $grid-color, $grid-color, 0.05) 1px,
      transparent 1px
    ),
    linear-gradient(
      to bottom,
      rgba($grid-color, $grid-color, $grid-color, 0.05) 1px,
      transparent 1px
    ),
    linear-gradient(
      to top,
      rgba($grid-color, $grid-color, $grid-color, 0.05) 1px,
      transparent 1px
    ),
    linear-gradient(
      to left,
      rgba($grid-color, $grid-color, $grid-color, 0.05) 1px,
      transparent 1px
    );
  text-rendering: optimizeLegibility;
  font-kerning: normal;
}

a.star::after {
  content: "*";
  font-size: small;
  position: relative;
  top: -0.4rem;
  opacity: 0.6;
}
